header{
    padding: 24px;
    .header-wrap{
        max-width: 1200px;
        min-width: 1000px;
        width: 100%;
        margin: 0 auto;
    .header-top{
        display: flex;
        justify-content: space-between;
        .header-left{
            display: flex;
            .logo{
            cursor: pointer;
            width: 128px;
            height: 45px;
            background-image: url('../pic/pinkoi.png');
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
            margin-right: 24px;
            }
            .header-search{
            display: inline-block;
            .search-text{
                width: 25vw;
                padding: 6px 32px 5px 14px;
                line-height: 1.4;
                border: 1px solid #d3d3d5;
                font-size: 1rem;
                background: #fff;
                color: #39393e;
                border-radius: 2px 0 0 2px;
                border-right: none;
                
            }
            .voice-icon{
                cursor: pointer;
                border: 1px solid #d3d3d5;
                border-right: none;
                border-left:none;
                padding: 7.9px 4px 7.3px 12px;
                i{
                  font-size: 20px;
                  color: #39393e;}
            }
            .submit{
                width: 70px;
                padding: 12px 6px 9px;
                background: #10567b;
                color: white;
                cursor: pointer;
                border-radius: 0 2px 2px 0;
                line-height: 1;
                border:none;
                &:hover{
                  background-color: #003354;
                }
                
            }
            .trend{
                
                position: relative;
                margin-top: 6px;
                
                a{
                  font-size: 13px;
                  color: #39393e;
                  margin-right:14px ;
                &:hover{color:#2e90b7;; }
                }
                
            }
        }
        }
        .header-right{
          
          width: 20vw;
          .navbar {
            
            .navbar-nav {
              display: flex;
              align-items: center;
              justify-content: center; 
              .nav-item {
                padding: 6px 18px;
                &:nth-child(1){
                  display: none;
                }
                  .icon-button { 
                    
                  svg { 
                  fill: var(--text-color);
                  width: 24px;
                  
                    }
                  svg:hover {
                    fill: #2e90b7;
                  }
                  
                    }
                    .dropdown {
                    position: absolute;
                    top: 60px;
                    width: 250px;
                    border: var(--border);
                    overflow: hidden;
                    background-color: #fff;
                    
                    .menu {
                      width: 100%;
                    
                      .menu-item {
                        height: 30px;
                        display: flex;
                        align-items: center;
                        
                        
                      }
                    .menu-item:hover {
                      text-decoration:underline;
                      
                    }
                  }
                  }
                  
          }
        } 
            }
        }
}




}
  
  

  

  


  
  .sticky-nav {
    z-index: 100;
    background-color: #fff;
    width: 100%;
    display: flex;
    height: 60px;
    justify-content: space-around;
    border-bottom: var(--border);
    position: fixed;
    top: 0;
    left: 0;
    ul{
      
      display: flex;
      li{
        color:var(--text-color);
        padding: 16px;
      }
    }
    .small-btn{
        .collect-btn{   
        line-height:60px;
        vertical-align: middle;  
        text-align: center;
        margin-right: 6px;
        width: 100%;
        padding: 9px 14px;
        color:#39393e;
        border-radius: 4px;
        border: #d3d3d5 1px solid;
        i{margin-right:4px ;
            color:#929295;
        }
        
    }
    .add-btn{
      
      
      text-align: center;
      width: 190px;
      color: #fff;
      background: #f16c5d;
      border: 1px solid #f16c5d;
      padding: 9px 13px;
      border-radius: 4px;
      i{margin-right:4px ;
          color: #fff;}
      }
    }
  }
  .cate-bar{
    border-bottom: var(--border);
    padding: 14px 0;
    
    ul{
      display: flex;
      justify-content: space-around;
      li{
        font-size: 14px;
        a{
          color:var(--text-color);
        }
        .cate-items {
          display: flex;
          flex-direction: column;
          position: absolute;
          top: 134px;
          height: auto;
          border: var(--border);
          overflow: hidden;
          background-color: #fff; 
          .cate-name:hover {
            text-decoration:underline;        

          }
        }
      }
    }
  }
}
  @media screen and (max-width: 555px){
  header{
    .header-wrap{
      
      .header-top{
      padding: 10px;
      z-index: 100;
      background-color: #fff;
      position: fixed;
      
      top: 0;
        .header-left{
          .logo{
          margin-left: 40px;
          cursor: pointer;
          width: 55px;
          height: 36px;
          }
          .trend{
          display: none;
          }
        }
        .header-right{
          .navbar{
            .navbar-nav{
              .nav-item{
                padding: 6px;
              }
              .nav-item:nth-child(2){
                display: none;
              }
              .nav-item:nth-child(3){
                display: none;
              }
              .nav-item:nth-child(1){
                display: inline-block;
              }
            }
          }
          .sticky-nav{
          top:48px;
          :nth-child(2){
          display: none;
          }
            .small-btn{
              display: none;
            }
          }
        }
      }
      .cate-bar{
        display: none;
      }
  }
    
  }
}
    
